<template>
  <div id="myEvaluate">
    <div class="myEvaluate_ball">
      <div class="text">整体评价</div>
      <div @click="evaluate(0)">
        <img v-if="'好评，中评，差评'.includes(myEvaluate_text)" src="../common/images/好评@2x.png">
        <img v-else src="../common/images/差评@2x.png">
      </div>
      <div @click="evaluate(1)">
        <img v-if="'好评，中评'.includes(myEvaluate_text)" src="../common/images/好评@2x.png">
        <img v-else src="../common/images/差评@2x.png">
      </div>
      <div @click="evaluate(2)">
        <img v-if="'好评'.includes(myEvaluate_text)" src="../common/images/好评@2x.png">
        <img v-else src="../common/images/差评@2x.png">
      </div>
    </div>
    <div class="myEvaluate_text">{{myEvaluate_text}}</div>
  </div>
</template>

<script>
export default {
  name: 'myEvaluate',
  props: {
    myEvaluate_text: {
      type: String,
      default: '中评'
    }
  },
  data () {
    return {}
  },
  methods: {
    evaluate (index) {
      let textE
      switch (index) {
        case 0:
          textE = '差评'
          break
        case 1:
          textE = '中评'
          break
        case 2:
          textE = '好评'
          break
      }
      this.$emit('evaluate', index, textE)
    }
  }
}
</script>

<style scoped>
  #myEvaluate {
    height: 4rem;
    width: 100%;
    position: relative;
    background-color: white;
  }

  #myEvaluate .myEvaluate_ball {
    display: inline-block;
  }

  #myEvaluate .myEvaluate_ball .text {
    background-color: #FFFFFF;
    width: 6rem;
    margin: 0;
    position: relative;
    bottom: 0.5rem;
    text-align: center;
    font-weight: 600;
  }

  #myEvaluate .myEvaluate_ball div {
    display: inline-block;
    width: 2rem;
    margin: 1rem 0.2rem;
  }

  #myEvaluate .myEvaluate_ball div img {
    width: 2rem;
  }

  #myEvaluate .myEvaluate_text {
    display: inline-block;
    text-align: right;
    position: absolute;
    right: 1rem;
    height: 4rem;
    line-height: 4rem;
    color: black;
    font-size: 1.2rem;
  }
</style>



