<template>
  <div id="myTextarea" ref="myTextarea">
    <textarea class="myTextArea" :placeholder="PlaceHolder" v-model="text_value"></textarea>
  </div>
</template>

<script>
export default {
  name: 'myTextarea',
  props: {
    height: {
      type: String,
      default: '12rem'
    },
    PlaceHolder: {
      type: String,
      default: '可以写下对服务人员的评价'
    }
  },
  data () {
    return {
      text_value: ''
    }
  },
  watch: {
    text_value: {
      handler (newVal, oldVal) {
        this.$emit('inputChange', newVal)
      },
      immediate: true
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      let myTextarea = this.$refs.myTextarea
      myTextarea.style.height = this.height
    }
  }
}
</script>

<style scoped>
  #myTextarea {
    position: relative;
    height: 8rem;
    width: 100%;
  }

  #myTextarea .myTextArea {
    width: 100%;
    height: 100%;
    left: 0;
    border: 0;
    position: absolute;
    font-size: 1.2rem;
    padding: 1rem;
  }
</style>



