<template>
  <div id="evaluate">
    <my-service-p class="myServiceP" :reputablyShow="false" :data="service_data" @clickC="clickC"
                  :advisory-show="false"></my-service-p>
    <my-evaluate class="myEvaluate" @evaluate="evaluate" :myEvaluate_text="myEvaluate_text"></my-evaluate>
    <my-textarea class="myTextarea" @inputChange="inputChange"></my-textarea>
    <div class="myEvaluate_button" @click="submitB">提交</div>
  </div>
</template>

<script>
import myServiceP from '../../../components/myServiceP'
import myEvaluate from '../../../components/myEvaluate'
import myTextarea from '../../../components/myTextarea'

export default {
  name: 'evaluate',
  components: {
    myServiceP,
    myEvaluate,
    myTextarea
  },
  data () {
    return {
      service_data: JSON.parse(localStorage.getItem('serviceData')),
      myEvaluate_text: '中评',
      text_value: '',
      toastShow: 0,
      toastContent: ''
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      this.$store.commit('setCurrentTitle', '评价')
    },
    submitB () {
      if (this.text_value === '' || !this.text_value) {
        this.$Dialog.Toast({
          content: '请完成服务评价再进行提交',
          title_show: false
        })
        return
      }
      console.log(this.myEvaluate_text, this.text_value)
      this.$router.push({
        name: 'EvaluationSuccess'
      })
    },
    inputChange (val) {
      this.text_value = val
    },
    evaluate (index, value) {
      this.myEvaluate_text = value
    },
    // 关注或者取消关注
    clickC (val) {
      if (val) {
        this.service_data.concerned = false
      } else {
        this.service_data.concerned = true
      }
    },
    evaluateQuery () {
      this.$http.evaluate({
        id: '',
        star: 3,
        content: '',
        status: 1
      }).then((res) => {})
    }
  }
}
</script>

<style scoped>
  #evaluate {
    background-color: #F6F7F8;
    position: fixed;
    width: 100%;
    top: 3rem;
    bottom: 0;
  }

  #evaluate .myServiceP {
    background-color: #FFFFFF;
  }

  #evaluate .myEvaluate {
    background-color: #FFFFFF;
    border-bottom: 0.5px solid #EEEEEE;
  }

  #evaluate .myEvaluate_button {
    width: 23rem;
    height: 3rem;
    text-align: center;
    line-height: 3rem;
    background-color: #E73E3A;
    color: #FFFFFF;
    margin: 4rem 1rem;
    border-radius: 4px;
  }
</style>



